import type { Controller } from '@ali/4ever-cangjie';
import { removeHighlightSingle } from '../utils';
import type { IPayload } from '../actions';

export default function removeHighlight(controller: Controller, payload: IPayload, isHighlightBorder: boolean) {
  if (isHighlightBorder) {
    const { node, type, reason } = payload;
    const nodes = Array.isArray(node) ? node : [node];
    nodes.forEach((n) => {
      removeHighlightSingle(n, type, reason);
    });
  }
}